p,h1,h2,h3,h4,h5,h6{margin:0}ul,ol{list-style:none;padding-left:0;margin:0}a{text-decoration:none}h1,h2,h3,h4,h5,h6{font-family:'Titan One', cursive}.visually-hidden{position:absolute;width:1px;height:1px;margin:-1px;border:0;padding:0;white-space:nowrap;clip-path:inset(100%);clip:rect(0 0 0 0);overflow:hidden}.is-hidden{visibility:hidden;opacity:0;pointer-events:none}body{font-family:'DM Sans', sans-serif;font-size:16px;letter-spacing:0.04em}.no-scroll{overflow:hidden}.container{padding-left:20px;padding-right:20px;margin:0 auto;max-width:320px}@media screen and (min-width: 768px){.container{padding-left:32px;padding-right:32px;max-width:768px}}@media screen and (min-width: 1280px){.container{padding-left:16px;padding-right:16px;max-width:1280px}}img{max-width:100%;height:auto;display:block}.section{padding-top:40px;padding-bottom:40px}@media screen and (min-width: 1280px){.section{padding-bottom:60px;padding-top:60px}}.backdrop{position:fixed;top:0;width:100%;height:100%;background-color:rgba(0,0,0,0.2);transition:opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),visibility 250ms cubic-bezier(0.4, 0, 0.2, 1)}.button{display:flex;justify-content:center;align-items:center;text-align:center;font-family:"DM Sans",sans-serif;color:#d41443;cursor:pointer;margin:0 auto;border:none}
